body {
    font-size: 16px;
    padding: 24px 12px;
}
@media screen and (min-width: 960px) {
    body {
        max-width: 960px;
        margin: 0 auto;
    }
}

h2 {
    margin: 0;
    font-size: 21px;
    margin-top: 48px;
    margin-bottom: 24px;
}

p,
li {
    line-height: 1.45;
}

.scale {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 24px;
}

.note {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 3px;
    min-width: 60px;
    min-height: 60px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: #EFEFEF;
}
.note.inScale {
    background: #CAEBF2;
}
.note.highlighted {
    border: 1px solid;
}
.noteFunction {
    font-size: 12px;
    font-weight: 600;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: end;
        align-items: flex-end;
    min-height: 24px;
    text-align: center;
}



